export default [
    {
        path:'/home',
        component:()=>import("@/views/Home"),
        meta:{show:true}
    },

    {
        path:'/timeTable',
        component:()=>import("@/views/classSchedule"),
        name:'timeTable',
        meta:{show:true}
    },

    // 绑定用户信息
    {
        path:'/bindUserInfo',
        component:()=>import("@/views/bindUserInfo"),
        name:'bindUserInfo',
    },

    //重定向，在项目跑起来的时候，访问/，立马定向到首页
    {
        path:'*',
        redirect:'/home'
    }
]